@import "_imports.scss";

// TYPOGRAPHY

@import "fonts/_ostrich-sans.scss";
@import "fonts/_Source_Sans.scss";
@import "fonts/_Ek-Mukta.scss";
//@import "fonts/_Clear_Sans.scss";
//@import "fonts/_computer-modern-bright.scss";
//@import "fonts/_Aleo-Light.scss";
//@import "fonts/_League_Spartan.scss";

$tablet-width: 768px;
$desktop-width: 1024px;
$breakpoint: 768px;

@mixin breakpoint {
  @media (min-width: #{$breakpoint}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

@mixin retina {
  @media
  only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (-o-min-device-pixel-ratio: 2/1),
  only screen and (min-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi),
  only screen and (min-resolution: 2dppx) {
    @content;
  }
}

@mixin print {
  @media print {
    @content;
  }
}

$dj-header-font: "Ostrich Sans";
$dj-header-font-weight: 400;
$dj-body-font: "Ek-Mukta";
$dj-body-font-weight: 100;

// from http://www.thedayscolor.com/wallpapers/052614_pgso.jpg
$dj-white: #F8F8F8;
$dj-light-gray: #C2C3C5;
$dj-medium-gray: #686868;
$dj-black: #161616;
$dj-blue: #2B418C;

$dj-white: hsl(0, 0%, 97%);
$header-height: 12rem;
$header-height-desktop: 6rem;
$footer-height: 3rem;

body {
  font-family: $dj-body-font;
  font-weight: $dj-body-font-weight;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $dj-header-font;
  font-weight: $dj-header-font-weight;
}

// LAYOUT
//$susy: (
//  columns: 12,  // The number of columns in your grid
//  gutters: 1/4, // The size of a gutter in relation to a single column
//);

* {
  box-sizing: border-box;
}

section a, article a, footer a {
  position: relative;
  text-decoration: none;
  white-space: nowrap;
  word-break: keep-all;

  &:hover {
    &:before {
      height: 2px;
      opacity: .8;
      transform: scaleX(1);
      bottom: 4px;
    }
  }

  &:before {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    transform: scaleX(.75);
    bottom: 2px;
    left: 0;
    background-color: $dj-black;
    opacity: .1;
    transition: all 175ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
}

body {
  margin: 0px;
  background-color: $dj-white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  > section, article {
    flex: 0 1 auto;
    min-height: calc(100vh - #{$header-height + $footer-height});
    width: 100%;
    padding: 2em 0;

    @include breakpoint {
      min-height: calc(100vh - #{$header-height-desktop + $footer-height});
    }
  }
}

footer {
  flex: 0 0 auto;
  position: relative;
  width: 100%;
  height: $footer-height;
  background-color: inherit;
  color: $dj-white;
  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    bottom: .65em;
    left: 0;
    width: 100%;
    background-color: $dj-black;
    height: .25em;
  }

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: $dj-black;
    height: .5em;
  }
}

header {
  flex: 0 0 auto;
  width: 100%;
  height: $header-height;
  font-family: $dj-header-font;
  font-weight: 400;
  font-size: 3.5rem;
  line-height: 1;

  @include breakpoint {
    height: $header-height-desktop;
  }
}

html {
  min-height: 100vh;
}

nav {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  padding: 0 .1em;

  &:before {
    content: "";
    position: absolute;
    bottom: .1em;
    left: 0;
    width: 100%;
    background-color: $dj-black;
    height: .05em;
  }

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: $dj-black;
    height: .025em;
  }

  > a {
    text-decoration: none;
    color: $dj-black;

  }
}

// STLYES

img {
  max-width: 100%;
}

p {
  font-size: 1.15rem;
}

.bq { @include typl8-blockquote("—"); }

.bravado {
  $font-size:  map-get($typl8-scale,"typl8-tera");
  font-size: $font-size + px;
  font-weight: normal;
  font-style: normal;
  width: 3em;
  text-align: center;
  word-break: break-word;
  margin: 0 auto .25em;
  overflow: hidden;

  @include desktop() {
    margin: .10vh auto;
  }

  > h1 {
    font-size: inherit;
    width: 1.75em;
    margin: 0;

    > span {
      display: block;
      width: 1.75em;
      margin: 0 0 0 1.25em;
    }
  }
}



.center {
  text-align: center;
}

blockquote + figcaption cite {
  display: inline;
}

.hidden {
  display: none;

  &:before, &:after {
    display: none;
  }
}

.mainContent:focus {
  outline: none;
}

.nav-links {
  display: flex;
  flex: 2 1 auto;
  flex-direction: column;
  justify-content: space-around;
  font-size: 85%;

  @include breakpoint {
    flex-direction: row;
  }

  > a {
    padding: .05em 0;
    text-decoration: none;
    color: $dj-blue;

    > span {
      position: relative;

      &:hover {
        &:before {
          height: 2px;
          opacity: .8;
          transform: scaleX(1);
          bottom: 6px;
        }
      }

      &:before {
        content: "";
        position: absolute;
        height: 1px;
        width: 100%;
        transform: scaleX(.75);
        bottom: 3px;
        left: 0;
        background-color: $dj-blue;
        opacity: .1;
        transition: all 175ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
      }
    }
  }
}

.projects-box {
  min-height: 225px;

  $gif-width: 360px;

  @include breakpoint {
    width: 3 * $gif-width;
    max-width: 100%;
    margin: 0px auto;
  }

  div {

    @include breakpoint {
      width: 32vw;
      max-width: $gif-width;
      float: left;
    }
  }

  img {
    display: block;
    margin: 0 auto;
    position: relative;


  }

  .face-wrap {
    background-color: #000;
  }

  .tech-ethics-wrap {
    background-color: #FFF;
  }

  .cnc-wrap {
    background-color: inherit;
  }
}

.projects-header {
  margin: 1rem 0 2rem;
  text-align: center;
}

.tagline {
  margin: 3rem;
  text-align: center;
}

#projects-nav {
  > span {
    &:hover {
      &:before {
        height: 2px;
        opacity: 1;
        transform: scaleX(1);
      }
    }

    &:before {
      transform: scaleX(.875);
    }
  }
}

#logo {
  flex: 1 1 50%; // does not render properly on iPhone with auto
  padding: 0 .25em;

  @include breakpoint {
    flex: 1 1 auto;
  }
  
  > span {

  }
}

